<template>
    <div class="intro-y box">
        <div class="flex flex-col sm:flex-row items-center p-5 border-b border-slate-200/60 dark:border-darkmode-400">
            <h2 class="font-medium text-base mr-auto">
                Edit User Form
            </h2>
        </div>
        <div id="vertical-form" class="p-5">
            <div class="preview">
                <form v-on:submit.prevent="onSubmit" method="post">
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">First Name</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="First Name" required
                            v-model="posts.firstName" name="firstName">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Last Name</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="Last Name" required
                            v-model="posts.lastName" name="lastName">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Email</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="email@gmail.com" required
                            v-model="posts.email">

                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Mobile Number</label>
                        <input id="vertical-form-2" type="text" class="form-control" placeholder="Mobile Number" required
                            v-model="posts.phoneNumber" name="phoneNumber">
                    </div>
                    <div class="mt-3">
                        <label for="vertical-form-2" class="form-label">Password</label>
                        <input id="vertical-form-2" class="form-control" required :type="showPassword ? 'text' : 'password'"
                            v-model="posts.password" name="password">
                        <div class="input-group-append">
                            <button class="btn btn-outline-secondary" type="button" @click="toggleShowPassword">
                                {{ showPassword ? 'Hide' : 'Show' }}
                            </button>
                        </div>
                    </div>
                    <button class="btn btn-primary mt-5" type="submit">Edit</button>
                </form>
            </div>
        </div>
    </div>
</template>


<script>
import { getRequestById, putRequest } from '../../pages/ApiFunctions';

export default {
    name: "EditUser",
    data() {
        return {
            showPassword: false,
            userId: '',
            posts: {
                firstName: "",
                lastName: "",
                email: "",
                phoneNumber: "",
                password: '',

            }
        }
    },
    mounted() {
        this.userId = this.$route.params.id
        this.getUserById(this.$route.params.id)
    }
    ,

    methods: {
        async onSubmit() {
            await putRequest(`/update-user/${this.userId}`, this.posts)
            this.$router.push({ name: 'UserDataList', })

        },

        async getUserById(id) {
            const res = await getRequestById(`/get-user/id/${id}`)
            this.posts.email = res.data.email
            this.posts.firstName = res.data.firstName
            this.posts.lastName = res.data.lastName
            this.posts.phoneNumber = res.data.phoneNumber


        },
        toggleShowPassword() {
            this.showPassword = !this.showPassword;
        },

    }
}
</script>


